import { useSearchParams, useLocation, useParams } from "react-router-dom";
import { useQuery } from 'react-query';
import { Result, Table } from 'antd';
import Breadcrumb from '../../components/breadcrumbs';
import CampaignSelect from '../../components/campaign-select';
import { useState } from 'react';
import { css } from '@emotion/react';


const LeadPage = () => {
    let [searchParams, setSearchParams] = useSearchParams();
    const location = useLocation();
    const { id } = Object.fromEntries(new URLSearchParams(location.search));


    const [pagination, setPagination] = useState({
        current: 1,
        pageSize: 20,
    })

    const [campaignId, setCampaignId] = useState(id);

    const { data, isLoading } = useQuery(["lead", campaignId, pagination], async () => {
        const { data } = await axios.get("/api/admin/lead/" + campaignId, {
            params: {
                page: pagination.current,
                pageSize: pagination.pageSize
            }
        });
        setPagination({
            ...pagination,
            total: data.total
        });
        return data.data;
    }, {
        enabled: !!campaignId
    })

    const handleChangeCampaign = (value) => {
        setSearchParams({ id: value })
        setCampaignId(value);
    }

    const handleTableChange = (pagination, filters, sorter) => {
        setPagination(pagination)
    };

    return (
        <div>
            <Breadcrumb items={["Lead"]} >
                <CampaignSelect
                    css={css`width:200px;`}
                    value={campaignId}
                    onChange={handleChangeCampaign}
                />
            </Breadcrumb>
            <Table
                rowKey={record => record.id}
                pagination={pagination}
                onChange={handleTableChange}
                columns={[
                    { title: "STT", render: (text, record, index) => index + 1 },
                    { title: "H tn", dataIndex: "name" },
                    { title: "ST", dataIndex: "phone" },
                    { title: "Email", dataIndex: "email" },
                    { title: "Ref", dataIndex: "ref" },
                    { title: " mi", dataIndex: "invited_count" },
                ]}
                dataSource={data}
                loading={isLoading}
            />
        </div>
    )
}

export default LeadPage;
